import config from './engagement-approval-list.config.json'
import JsonViewer from '@/components/JsonViewer'
export default {
    name: 'engagement-list',
    data() {
        return {
            config,
            engagements: [],
            errorMessage: ''
        }
    },
    components: {
        JsonViewer
    },
    methods: {
        getEngagements() {
            this.$http({
                method: 'get',
                url: '/engagement/manage-engagement/list-of-engagement',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                this.engagements = response.data
            })
            .catch(error => {
                this.errorMessage = error.response.data
            })
        },
        getStatus(_status) {
            let status = ''
            switch (_status) {
                case 's':
                    status = 'SUBMITTED'
                    break
                case 'a':
                    status = 'APPROVED'
                    break
                case 'r':
                    status = 'REJECTED'
                    break
                case 'd':
                    status = 'DISBURSED'
                    break
                default:
                    status = ''
            }
            return status
        }
    },
    created() {
        this.getEngagements()
    }
}